/*** Poverty Report ***/
.report-processing-msg {
  display: none;
  color: #f00;
  font-weight: 700;
  font-size: 1.2em;
}
#nyscaa-report-selection {
  margin-bottom: 20px;
}
#nyscaa-report-selection select {
  margin-left: 20px;
}
#nyscaa-report-selection .nyscaa-report-type {
  margin-bottom: 20px;
}
#nyscaa-report-selection .nyscaa-report-type a {
  cursor: pointer;
}
#nyscaa-report-content {
  clear: both;
  font-size: 100%;
  margin-bottom: 20px;
}
#nyscaa-report-content p {
  margin-bottom: 15px;
  font-size: 13px;
  line-height: 1.4em;
}
#nyscaa-report-content a {
  color: #002395;
}
#nyscaa-report-content .center-align {
  text-align: center;
}
#nyscaa-report-content #nyscaa-report-left {
  float: left;
  padding-right: 10px;
}
#nyscaa-report-content #nyscaa-report-right {
  float: left;
}
#nyscaa-report-content #nyscaa-report-title {
  float: left;
}
#nyscaa-report-content #nyscaa-report-datenote {
  float: left;
}
#nyscaa-report-content #nyscaa-report-title1 {
  font-family: Impact, sans-serif;
  font-size: 25pt;
  font-weight: 700;
  color: #002395;
  text-align: center;
}
#nyscaa-report-content #nyscaa-report-title2 {
  margin-top: 18px;
  text-align: center;
}
#nyscaa-report-content #nyscaa-report-censusbox {
  border: 1px solid #002395;
  color: #00B0F0;
  padding: 5px;
  font-size: 12pt;
  font-weight: 500;
  text-align: center;
  margin-bottom: 10px;
}
#nyscaa-report-content .nyscaa-report-datatitle {
  font-size: 18pt;
  font-weight: 500;
  color: #002395;
  font-family: Impact, sans-serif;
  line-height: 1.2em;
  letter-spacing: 1px;
}
#nyscaa-report-content .nyscaa-report-datatitle2 {
  font-size: 12pt;
}
#nyscaa-report-content #nyscaa-report-edupov {
  padding: 0 35px;
  display: inline-block;
  vertical-align: middle;
}
#nyscaa-report-content #nyscaa-report-datakey-2,
#nyscaa-report-content #nyscaa-report-datakey-3 p,
#nyscaa-report-content #nyscaa-report-datakey-5 {
  clear: both;
}
#nyscaa-report-content #nyscaa-report-datakey-2 p {
  margin-top: 10px;
}
#nyscaa-report-content .nyscaa-report-datakey-note {
  color: #002395;
  font-weight: bold;
}
@media screen and (min-width: 700px) {
  #nyscaa-report-content #nyscaa-report-left {
    width: 28%;
    padding-right: 10px;
  }
  #nyscaa-report-content #nyscaa-report-right {
    border-left: 5px solid #00B0F0;
    width: 68%;
    float: left;
    padding-left: 10px;
  }
  #nyscaa-report-content #nyscaa-report-title {
    width: 30%;
  }
  #nyscaa-report-content #nyscaa-report-datenote {
    width: 65%;
  }
  #nyscaa-report-content #nyscaa-report-wagekey {
    float: left;
    width: 42%;
    padding: 10px 20px 0 0;
  }
  #nyscaa-report-content #nyscaa-report-datakey-41 {
    float: left;
    width: 54%;
  }
}
.nyscaa-report-action {
  clear: both;
  margin-bottom: 20px;
}
